nav {
    position: sticky;
    top: 0;
    width: 100%;
    margin: 0;
    background: #041E2C;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: center;
    /*border-bottom: 2px solid black;*/
    /*border-bottom: 2px solid #122646;*/
}

.desktop-nav {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin: 0;
    padding: 0;
    font-size: 1.3em;
    font-weight: 800;
    list-style: none;
}

.mobile-nav {
    position: absolute;
    top: 90px;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-items: center;
    margin: 0;
    padding: 0;
    background: #041E2C;
    box-shadow: 0px 0px 10px  black;
    font-size: 1.3em;
    font-weight: 800;
    list-style: none;
}

.mobile-nav.closed {
    display: none;
}

nav li {
    padding: 0.9em 1.5em;
}

#download {
    background: #005684;
    box-shadow: -2px 2px 3px  black;
    border-radius: 5px ;
    padding: 0.9em;
    height: 1em;
}

nav a {
    text-decoration: none;
    color: #d8d4cf;
}

.inactive {
    /*color: white;*/
}
.active {
    color: #FFBE70;
}

